* {
  box-sizing: border-box;
}

.container {
  box-sizing: border-box;
  width: 100%;
  height: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-right: 50px;

  @media screen and (max-width: 460px) {
    padding-right: 5px;
  }

  .container__img {
    box-sizing: border-box;
    width: 80px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;

    .link {
      width: 60px;
      height: 60px;
      transform: scale(1);
      transition: transform 0.6s;

      .logo__img {
        width: 60px;
        height: 60px;
        border-radius: 50%;

      }
    }

    .active {
      width: 60px;
      height: 60px;
      transform: scale(1);
      transition: transform 0.6s;

      .logo__img {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background-color: #fff;
      }
    }

    .link:hover {
      transform: scale(1.1);
      transition: transform 0.6s;
    }

    .active:hover {
      transform: scale(1.1);
      transition: transform 0.6s;
    }
  }
}